body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nameholder {
    position: relative;
    display: inline-block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background: #e00043;
    color: #fff;
    margin: 0;
    /* line-height: 75px; */
    border-radius: 10px;
    padding: 20px;
    font-size: xx-large;
    margin-bottom: 10px;
    margin-right: 10px;
    transition: all 0.5s ease;
    border: #e00043 solid 5px;
    cursor: pointer;
    /* float: left; */
}
#PhiriyeDao {
    position: fixed;
    border-radius: 50%;
    height: 25px;
    top: 10px;
    left: 10px;
    line-height: 15px;
}
.namebloc {
    text-align: center;
    display: inline-block;
    margin-bottom: 10px;
    position: fixed;
    top: 45%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.child {
    font-size: large;
    display: inline-block;
    /* transform: scale(0.75,0.75); */
    /* float: left; */
}
.nameholder:hover {
    color: #e00043;
    background: #fff;
}
